.footer {
    position: relative;
    flex-direction: column;
    height: auto;
    min-height: 70px;
    padding: 15px;
    text-align: center;
    color: #a8b2d1;
    font-family: "JetBrains Mono", monospace;
    font-size: .8rem;
    margin-top: 70px;
}

.footer::before {
    content: "";
    display: block;
    width: 50%;
    margin: 10px auto;
    position: relative;
    width: 20em;
    height: 1px;
    background-color: #233554;
    color: #a8b2d1;
}

.footer-icons {
    margin-top: 10px;
}

.footer-icon {
    color: #a8b2d1;
    width: 18px;
    height: 18px;
    margin-left: 10px;
}

.footer-icon:hover {
    color: #66CCFF;
}